import React, { useState } from "react";
import "./About.css";

const About = () => {
  const [flipped, setFlipped] = useState(false);

  // Funkce pro pepnut stavu karty (flip)
  const handleClick = () => setFlipped(!flipped);

  return (
    <div className="about-page">
      <div className="about-card-container">
        <div
          className="about-card"
          onClick={handleClick} // Flip karta pi kliknut
          style={{
            // perspective: "1000px", // Aby se efekt flipu vykonal v 3D prostoru
          }}
        >
          <div
            className={`about-card-inner ${flipped ? "flipped" : ""}`}
            style={{
              transition: "transform 0.6s", // Hladk pechod pi otoen
              transformStyle: "preserve-3d", // Udruje 3D efekt
            }}
          >
            <div className="about-card-front">
              <h3>Front Side</h3>
              <p>This is the front of the card.</p>
            </div>
            <div className="about-card-back">
              <h3>Back Side</h3>
              <p>This is the back of the card.</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;